import { onMounted, onUnmounted } from 'vue';
import { randomString } from '@/utils/util';

/**
 * Set
 * 
 */
const lendStrings = new Set<string>();

/**
 * ## ID
 *
 * 
 * unmount
 * SSRID
 *
 * @returns `a-z,A-Z,0-9`
 */
export default () => {
  let id = randomString();

  onMounted(() => {
    while (lendStrings.has(id)) {
      id = randomString();
    }
    lendStrings.add(id);
  });

  onUnmounted(() => {
    lendStrings.delete(id);
  });

  return id;
};
